<template >
  <div class="header">
    <img :src="logo" class="logo" alt="logo" title="logo" />
  </div>
  <h2 class="weather-heading mb-0">
    <span class="color-1">Weather</span> <span class="color-2">App</span>
  </h2>
  <v-container class="text-white lighten-5" text-xs-center>
    <v-row no-gutters>
      <v-col cols="12" sm="6" class="mx-auto">
        <v-divider color="#fff" class="my-5"></v-divider>
        <v-autocomplete
          v-model="location"
          :items="items"
          :loading="isLoading"
          v-model:search="search"
          color="#fff"
          hide-no-data
          hide-selected
          item-text="name"
          item-value="lat"
          label="Enter Location"
          placeholder="Start typing to Search"
          return-object
          prepend-icon="mdi-map-marker-multiple"
        ></v-autocomplete>
        <v-btn class="w-100 text-h5" color="#a5d6a7" @click="handleLoad">
          SHOW
        </v-btn>
        <p class="mt-2 error">
          <span v-show="errorMsg">It seems location is not entered.</span>
        </p>
      </v-col>
    </v-row>

    <br />
    <div v-if="errors" class="error">
      {{ errors[0] }}
    </div>
    <v-row no-gutters v-if="weatherInfo && weatherInfo.name">
      <v-col cols="12" sm="6" class="mx-auto">
        <v-card elevation="15" outlined tile>
          <div v-if="weatherInfo && weatherInfo.weather">
            <div class="d-flex flex-row mt-4">
              <div class="pa-2 mx-2">
                <p>
                  <strong class="text-h6">{{ weatherInfo.name }}</strong> ,
                  {{ this.location }}
                </p>
                <p>{{ weatherInfo.weather[0].description }}</p>
              </div>
              <v-spacer></v-spacer>

              <img
                class="pa-2 icon-border mx-2"
                :src="
                  'http://openweathermap.org/img/wn/' +
                  weatherInfo.weather[0].icon +
                  '@2x.png'
                "
                height="70"
                width="70"
              />
            </div>
            <br />
          </div>
          <v-divider></v-divider>

          <v-row v-if="weatherInfo" no-gutters class="px-5 pb-5 pt-5">
            <v-col cols="12" sm="4" class="mx-auto">
              Temprature:
              {{ weatherInfo.main && weatherInfo.main.temp }} &deg;C
            </v-col>
            <v-col cols="12" sm="4" class="mx-auto">
              &#9660;(Min):
              {{ weatherInfo.main && weatherInfo.main.temp_min }} &deg;C
            </v-col>
            <v-col cols="12" sm="4" class="mx-auto">
              &#9650;(Max):
              {{ weatherInfo.main && weatherInfo.main.temp_max }} &deg;C
            </v-col>
          </v-row>
          <v-divider class="mb-4"></v-divider>

          <v-row v-if="weatherInfo" no-gutters class="px-5 pb-5">
            <v-col cols="12" sm="4" class="mx-auto">
              Visibility: {{ weatherInfo.visibility }} meter
            </v-col>
            <v-col cols="12" sm="4" class="mx-auto">
              Wind Speed: {{ weatherInfo.wind && weatherInfo.wind.speed }} m/s
            </v-col>
            <v-col cols="12" sm="4" class="mx-auto">
              Humidity: {{ weatherInfo.main && weatherInfo.main.humidity }} %
            </v-col>
          </v-row>
          <Transition>
            <div v-if="showMore">
              <v-divider class="mb-4"></v-divider>
              <v-row no-gutters class="px-5 pb-5">
                <v-col cols="12" sm="4" class="mx-auto">
                  Temp(feels like):
                  {{ weatherInfo.main && weatherInfo.main.feels_like }} &deg;C
                </v-col>
                <v-col cols="12" sm="4" class="mx-auto">
                  Atmospheric Pressure:
                  {{ weatherInfo.main && weatherInfo.main.pressure }} hPa
                </v-col>
                <v-col cols="12" sm="4" class="mx-auto">
                  Weather Parameter:
                  {{ weatherInfo.weather && weatherInfo.weather[0].main }}
                </v-col>
              </v-row>
              <v-divider class="mb-4"></v-divider>
              <v-row no-gutters class="px-5 pb-5">
                <v-col cols="12" sm="4" class="mx-auto">
                  Wind Direction:
                  {{ weatherInfo.wind && weatherInfo.wind.deg }} &deg;
                </v-col>
                <v-col cols="12" sm="4" class="mx-auto">
                  Cloudiness:
                  {{ weatherInfo.clouds && weatherInfo.clouds.all }} %
                </v-col>
                <v-col cols="12" sm="4" class="mx-auto">
                  Sunrise:
                  {{ showDate(weatherInfo.sys && weatherInfo.sys.sunrise) }}
                </v-col>
              </v-row>
              <v-divider class="mb-4"></v-divider>
              <v-row no-gutters class="px-5 pb-5">
                <v-col cols="12" sm="4" class="mx-auto">
                  Sunset:
                  {{ showDate(weatherInfo.sys && weatherInfo.sys.sunset) }}
                </v-col>
                <v-spacer></v-spacer>
              </v-row>
              <v-divider class="mb-4"></v-divider>
            </div>
          </Transition>
          <p class="text-center my-4 pointer" @click="handleShowMore">
            {{
              showMore ? "&uarr; Show Less &uarr; " : "&darr; SHOW MORE &darr;"
            }}
          </p>
        </v-card>
      </v-col></v-row
    >
  </v-container>
</template>

<script>
import axios from "axios";
import image from "./../assets/logo.png";

export default {
  name: "WeatherComponent",
  data() {
    return {
      weatherInfo: [],
      errors: [],
      location: "",
      errorMsg: false,
      logo: image,
      entries: [],
      isLoading: false,
      search: null,
      showMore: false,
    };
  },
  methods: {
    async handleLoad() {
      if (!this.location) {
        this.errorMsg = true;
        return;
      }
      this.errorMsg = false;
      this.errors = [];
      let lat = this.entries[0].lat;
      let lng = this.entries[0].lon;
      let units = "metric";

      try {
        const response = await axios.get(
          `${process.env.VUE_APP_SERVICE_URL}data/2.5/weather?lat=${lat}&lon=${lng}&APPID=${process.env.VUE_APP_API_KEY}&units=${units}`
        );
        this.weatherInfo = response.data;
      } catch (e) {
        this.weatherInfo = [];
        this.errors.push(e.message);
      }
    },
    handleShowMore() {
      this.showMore = !this.showMore;
    },
    showDate(unix_timestamp) {
      // Create a new JavaScript Date object based on the timestamp
      // multiplied by 1000 so that the argument is in milliseconds, not seconds.
      var date = new Date(unix_timestamp * 1000);
      // Hours part from the timestamp
      var hours = date.getHours();
      // Minutes part from the timestamp
      var minutes = "0" + date.getMinutes();
      // Seconds part from the timestamp
      var seconds = "0" + date.getSeconds();

      // Will display time in 10:30:23 format
      return hours + ":" + minutes.substr(-2) + ":" + seconds.substr(-2);
    },
  },
  computed: {
    items() {
      return (
        this.entries &&
        this.entries.map((entry) => {
          let text = `${entry.name}, ${entry.state}, ${entry.country}`;
          return text;
        })
      );
    },
  },

  watch: {
    async search(city) {
      if (city.length < 1) return;
      this.isLoading = true;
      let limit = 5;
      // Lazily load input items
      try {
        const response = await axios.get(
          `${process.env.VUE_APP_SERVICE_URL}geo/1.0/direct?q=${city}&limit=${limit}&APPID=${process.env.VUE_APP_API_KEY}`
        );
        this.entries = response.data;
      } catch (e) {
        this.weatherInfo = [];
        this.errors.push(e.message);
      } finally {
        this.isLoading = false;
      }
    },
  },
};
</script>

